.version-label {
    position: absolute;
    bottom: 0px;
    left: 5px;
    font-size: .8em;
    opacity: 0.5;
    z-index: 1000;
    color: var(--ion-color-medium);
}

.dashboard {
    height: 100%;

    &-header {
        &-toolbar {
            ion-icon {
                height: 40px;
                width: 50px;
            }

            &-battery-icon {
                height: 100%;
                width: 50px;
                text-align: center;

                .battery {
                    &-container {
                        fill: var(--ion-color-primary-contrast);
                    }

                    &-gauge {
                        fill: var(--ion-color-primary-tint);
                    }

                    &-button {
                        fill: #ccc;
                    }

                    &-text {
                        fill: var(--ion-color-medium-shade);
                        font-size: 11px;
                    }
                }
            }

            &-logo {
                height: 40px;
                margin: 10px;
            }
        }
    }

    &-content {
        &-list {
            height: 100%;
            display: flex;
            flex-direction: column;
            align-content: center;

            &-spacer {
                flex: 1;
            }

            &-footer {
                margin-bottom: 20px;
            }

            &-header {
                color: var(--ion-color-medium);
                padding: 0px;

                ion-label {
                    margin: auto;
                }
            }

            &-item {
                height: 100%;
                display: flex;
                ion-slides {
                    height: 100%;
                    ion-slide {
                        flex-direction: column;
                        .swiper-slide-content {
                            width: 100%;
                            color: var(--ion-color-primary-contrast);
                        }
                        .swiper-slide-footer-text {
                            color: var(--ion-color-medium);
                            opacity: 0.3;
                        }
                        svg {
                            .circle {
                                fill: var(--ion-color-medium-tint);
                                opacity: .3;
                            }
                            .shadow {
                                display: none;
                            }
                            text {
                                fill: var(--ion-color-primary-contrast);
                            }
                        }
                    }
                    .swiper-slide-active {
                        svg {
                            width: 150%;
                            position: relative;
                            left: -25%;
                            .circle {
                                fill: var(--ion-color-primary);
                                opacity: 1;
                            }
                            .shadow {
                                display: inherit;
                                stroke-width: 1px;
                                opacity: .75;
                                fill: var(--ion-color-primary);
                                stroke: var(--ion-color-primary);
                            }
                            text {
                                fill: var(--ion-color-primary-contrast);
                            }
                        }
                        .swiper-slide-footer-text {
                            opacity: 1;
                        }
                    }
                    .swiper-slide-next,.swiper-slide-prev {
                    }
                }
            }
        }
    }
}